*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{

    ul{
        list-style: none;
    }

    a{
        text-decoration: none;
        color: inherit;
    }

    li{
        display: inline-block;
    }

    main{
        background-color: $secondary-background;
    }
}
